import { useQuery } from "react-query";
import { fetchCoinHistory } from "../api";
import ApexChart from "react-apexcharts";

interface IChart {
  coinId: string;
}

interface IHistorical {
  time_open: string;
  time_close: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
  market_cap: number;
}

function Chart({ coinId }: IChart) {
  const { isLoading, data } = useQuery<IHistorical[]>(["ohlcv", coinId], () =>
    fetchCoinHistory(coinId),
    { refetchInterval: 10000 }
  );

  return (
    <div>
      {isLoading ? (
        "Loading chart..."
      ) : (
        <>
          <ApexChart
            type="candlestick"
            series={[
              {
                name: "Price",
                data: data?.map((price) => {
                  return [price.time_close, price.open, price.high, price.low, price.close]
                }),
              },
            ]}
            options={{
              theme: {
                mode: "dark",
              },
              chart: {
                height: 300,
                width: 600,
                toolbar: {
                  show: false,
                },
                background: "transparent",
              },
              grid: { show: true },
              stroke: {
                curve: "smooth",
                width: 3,
              },
              yaxis: {
                show: true,
                labels: {
                  formatter: (v) => { return v.toFixed(0); }
                }
              },
              xaxis: {
                type: "datetime",
                categories: data?.map((price) => price.time_close),
              },
              fill: {
                type: "solid",
              },
              tooltip: {
                enabled: false
              }
            }}
          />
        </>
      )}
    </div>
  );
}

export default Chart;