import { useQuery } from "react-query";
import styled from "styled-components";
import { fetchCoinEvents } from "../api";

interface IEvent {
  coinId: string;
}

interface ICoinEvent {
  date: string
  date_to: string,
  description: string,
  id: string,
  is_conference: boolean,
  link: string,
  name: string,
  proof_image_link: string
}

const EventList = styled.ul``;
const Event = styled.li`
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 16px;
`;

function Price({ coinId }: IEvent) {
  let { isLoading, data } = useQuery<ICoinEvent[]>(
    ["events", coinId], () => fetchCoinEvents(coinId));
  return (
    <>
      {isLoading ? (
        "Loading Events..."
      ) : (
        <EventList>
          {data?.slice(-1).map(event =>
            <>
              <Event key={event.id}>
                Date: {event.date}
              </Event>
              <Event>
                Event Name: {event.name}
              </Event>
              <Event>
                Description: {event.description}
              </Event>
              <Event>
                Link: {event.link}
              </Event>
            </>
            )}
            </EventList>
          )}
        </>
      );
}

      export default Price;